*{
    margin: 0px;
    padding: 0px;

    --primary-color1:#3f82b1;
    --primary-color2:#484848;
    --primary-color3:#c6e7f1;
    --secondary-color1:#9a9a9a;
    --secondary-color2:#83a3b3;
    --secondary-color3:#ffffff;
    --tertiary-color1:#5c6569;
    --tertiary-color2:#506771;
    --tetriary-color3:#668491;
}

.teacher-nav-div{
    position: absolute;
    top: 72px;
    width: 100%;
    height: 500px;
    display: grid;
    grid-template-rows: repeat(7, 1fr);
}

.teacher-nav{
    background-color: var(--tertiary-color1);
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom-width:1px;
    border-bottom-color: var(--tetriary-color3);
    border-bottom-style: solid;
    box-sizing: border-box;
}

.teacher-nav:hover .teacher-nav-text{
    color: var(--secondary-color3);
    text-decoration-color: var(--secondary-color3);
}

.teacher-nav:hover{
    cursor: pointer;
}

.teacher-nav-text{
    font-size: 1.25em;
    font-weight: 100;
    text-decoration: underline;
    color: var(--primary-color3);
    text-decoration-color: var(--primary-color3);
    text-decoration-thickness: 1.5px;
    text-decoration-skip: 20px;
}

.teacher-login-register-div{
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 572px;
    height: 700px;
    width: 100%;
}

.teacher-login-register-box{
    position: absolute;
    display: block;
    height: 99%;
    width: 99%;
    border-radius: 20px;
    background-color: var(--tertiary-color1);
}

.teacher-intro{
    position: absolute;
    top: 1272px;
    width: 100%;
    height: 500px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.teacher-use-div{
    position: absolute;
    top: 1772px;
    height: 1700px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.teacher-use-nettuni{
    position: absolute;
    height: 99%;
    width: 100%;
    display: grid;
    grid-template-rows: repeat(5, 1fr);
}


.intro-div{
    position: absolute;
    height: 96%;
    width: 99%;
    border-radius: 20px;
    background-color: var(--tertiary-color1);
}

.teacher-intro-svg{
    position: absolute;
    top: 3%;
    height: 94%;
    width: 40%;
    display: grid;
    grid-template-rows: repeat(4,1fr);   
}

.teacher-vid-div{
    position: absolute;
    left: 40%;
    height: 100%;
    width: 60%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}


.teacher1, .teacher2, .teacher3, .teacher4{
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.teacher1{
    grid-row: 1 / span 1;
}

.teacher2{
    grid-row: 2 / span 1;
}

.teacher3{
    grid-row: 3 / span 1;
}

.teacher4{
    grid-row: 4 / span 1;
}

.teacher-svg-ene{
    position: absolute;
    height: 120px;
    width: 90px;
}


.teacher-use-1, .teacher-use-2, .teacher-use-3, .teacher-use-4, .teacher-use-5{
    position: absolute;
    border-radius: 10px;
    height: 94%;
    width: 99%;
    background-color: var(--tertiary-color1);
    left: 0.5%;
}

.teacher-use-1{
    grid-row: 1/span 1;
}

.teacher-use-2{
    grid-row: 2/span 1;
}

.teacher-use-3{
    grid-row: 3/span 1;
}

.teacher-use-4{
    grid-row: 4/span 1;
}

.teacher-use-5{
    grid-row: 5/span 1;
}









.teacher-login-info{
    position: absolute;
    height: 50%;
    width: 100%;
}

.teacher-form{
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 50%;
    top: 50%;
    width: 100%;
}

.teacher-login-div{
    position: absolute;
    height: 60%;
    width: 100%;
}

.teacher-signup-info{
    position: absolute;
    height: 40%;
    top: 60%;
    width: 100%;
}

.teacher-login-header{
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    border-top-right-radius: 20px;
    border-top-left-radius: 20px;
    height: 30%;
    width: 100%;
    background-color: var(--primary-color1);
}

.teacher-login-header-text{
    font-size: 1.5em;
    font-weight: 400;
    color: var(--secondary-color3);
}

.teacher-login-para{
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 36%;
    top: 30%;
    width: 100%;
}

.teacher-login-para-text{
    font-size: 1.1em;
    font-weight: 400;
    text-align: center;
    font-family: Georgia, 'Times New Roman', Times, serif;
    color: #d7d7d7;
    margin-top: 20px
}

.teacher-login-btn-div{
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 30%;
    top: 70%;
    width: 100%;
    border-bottom-color:var(--primary-color3);
    border-bottom-width: 1px;
    border-bottom-style: dotted;
}

.teacher-login-btn{
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 45px;
    width: 250px;
    border-radius: 10px;
    background-color: var(--primary-color1);
}

.teacher-login-btn-text{
    color: var(--primary-color3);
    font-size: 1.2em;
    font-weight: 550;
    user-select: none;
}

.teacher-login-btn:hover .teacher-login-btn-text{
    animation: change-color-login3 200ms 1;
    color: var(--tertiary-color1);
    font-size: 1.25em;
    font-weight: 550;
}

@keyframes change-color-login3 {
    0%{
        color: var(--primary-color3);
        font-size: 1.2em;
        font-weight: 550;
    }
    100%{
        color: var(--tertiary-color1);
        font-size: 1.25em;
        font-weight: 550;
    }
}

.teacher-login-btn:hover{
    animation: change-color-login4 200ms 1;
    background-color:var(--secondary-color2);
    user-select: none;
    cursor: pointer;
}

@keyframes change-color-login4 {
    0%{
      background-color:var(--primary-color1);
    }
    100%{
      background-color:var(--secondary-color2);
    }
}



.teacher-signup-header{
    position: absolute;
    height: 30%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.teacher-signup-header-text{
    font-size: 1.3em;
    font-weight: 400;
    font-family: Georgia, 'Times New Roman', Times, serif;
    text-align: center;
    color: var(--primary-color3);
}

.teacher-signup-para{
    position: absolute;
    height: 70%;
    top: 30%;
    width: 100%;
    display: flex;
    align-items: flex-start;
    justify-content: center;
}

.teacher-signup-para-text{
    font-size: 1.1em;
    font-weight: 400;
    font-family: Georgia, 'Times New Roman', Times, serif;
    text-align: center;
    color: #d7d7d7;
}

.teacher-form-center{
    position: absolute;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(4, 1fr);
    height: 98%;
    width: 90%;
}

.teacher-name-div, .teacher-email-div, .teacher-phone-div, .teacher-form-signup-info, .teacher-apply-div{
    position: absolute;
    display: flex;
    align-items: center;
    height: 100%;
    width: 100%;
}

.teacher-name-div{
    grid-column: 1 / span 1;
    grid-row: 1 / span 1;
    justify-content: flex-end;
    margin-right: 10px;
}

.teacher-email-div{
    grid-column: 1 / span 1;
    grid-row: 2 / span 1;
    justify-content: flex-end;
    margin-right: 10px;
}

.teacher-phone-div{
    grid-column: 1 / span 1;
    grid-row: 3 / span 1;
    justify-content: flex-end;
    margin-right: 10px;
}

.teacher-form-signup-info{
    grid-column: 2 / span 1;
    grid-row: 1 / span 3;
    justify-content: flex-start;
    margin-left: 10px;
}

.teacher-apply-div{
    grid-column: 1 / span 2;
    grid-row: 4 / span 1;
    justify-content: center;
}


.teacher-name, .teacher-email, .teacher-phone,.teacher-text-area {
    width: 400px;
    height: 60px;
    outline-width: 0px;
    margin-top: 5px;
    text-align: center;
    background-color:var(--primary-color2);
    color: var(--primary-color3);
    font-size: 0.85em;
    font-weight: 500;
    border-radius: 10px;
    border-color:var(--tertiary-color2);
    border-width: 2px;
    border-style: solid;
}

.teacher-text-area{
    font-size: 1em;
    font-weight: 500;
    height: 230px;
    width: 460px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.teacher-name::placeholder, .teacher-email::placeholder, .teacher-phone::placeholder,.teacher-text-area::placeholder{
    color: var(--primary-color3);
    font-size: 1.1em;
    font-weight: 450;
    font-family: Georgia, 'Times New Roman', Times, serif;
    
}

.teacher-name:focus, .teacher-email:focus, .teacher-phone:focus,.teacher-text-area:focus{
    color: var(--primary-color3);
    font-size: 0.9em;
    font-weight: 300;
    border-width: 3px;
    
}

.teacher-text-area:focus{
    font-size: 1.1em;
    font-weight: 300;
}

.teacher-text-area::placeholder{
    position: relative;
    top: 35%;
    font-size: 0.9em;
    font-weight: 360;
}

.apply-btn{
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 45px;
    width: 250px;
    border-radius: 10px;
    background-color: var(--primary-color1);
}
  
.apply-btn-text{
    color: var(--primary-color3);
    font-size: 1.2em;
    font-weight: 550;
    user-select: none;
}

.apply-btn:hover .apply-btn-text{
    animation: change-color-login3 200ms 1;
    color: var(--tertiary-color1);
    font-size: 1.25em;
    font-weight: 550;
}


.apply-btn:hover{
    animation: change-color-login4 200ms 1;
    background-color:var(--secondary-color2);
    user-select: none;
    cursor: pointer;
}



@media screen and (max-width:950px) {

    .teacher-intro-svg{
        width: 30%;  
    }
    
    .teacher-vid-div{
        left: 30%;
        width: 70%;
        justify-content: center;
    }


    .teacher-svg-text{
        font-size: 1em;
        font-weight: 400;
    }
    

    .teacher-login-btn{
        height: 35px;
        width: 210px;
        border-radius: 8px;
    }

    .teacher-login-para-text{
        font-size: 1em;
        font-weight: 400;
    }
    
    .teacher-login-btn-text{
        font-size: 1.1em;
        font-weight: 500;
    }

    .teacher-signup-header-text{
        font-size: 1.15em;
        font-weight: 400;
    }

    .teacher-signup-para-text{
        font-size: 0.9em;
        font-weight: 370;
    }

    .apply-btn{
        height: 35px;
        width: 210px;
        border-radius: 8px;
    }
      
    .apply-btn-text{
        font-size: 1.1em;
        font-weight: 500;
    }


}

@media screen and (max-width:600px) {
    .teacher-intro-svg{
        display: none;   
    }
    
    .teacher-vid-div{
        left: 0%;
        width: 100%;
        justify-content: center;
    }
    
    .teacher-login-info{
        height: 50%;
    }
    
    .teacher-form{
        height: 50%;
        top: 50%;
    }

    .teacher-login-div{
        height: 50%;
    }
    
    .teacher-signup-info{
        height: 50%;
        top: 50%;
    }

    .teacher-signup-header{
        height: 20%;
    }
    
    .teacher-signup-para{
        height: 80%;
        top: 20%;
        align-items: center;
    }

    .teacher-login-para-text{
        font-size: 0.9em;
        font-weight: 370;
    }

    .teacher-form-center{
        grid-template-columns: repeat(1, 1fr);
        grid-template-rows: repeat(7, 1fr);
    }

    .teacher-name-div{
        grid-column: 1 / span 1;
        grid-row: 1 / span 1;
        justify-content: center;
    }
    
    .teacher-email-div{
        grid-column: 1 / span 1;
        grid-row: 2 / span 1;
        justify-content: center;
    }
    
    .teacher-phone-div{
        grid-column: 1 / span 1;
        grid-row: 3 / span 1;
        justify-content: center;
    }
    
    .teacher-form-signup-info{
        grid-column: 1 / span 1;
        grid-row: 4 / span 3;
        justify-content: center;
        align-items: center;
    }
    
    .teacher-apply-div{
        grid-column: 1 / span 1;
        grid-row: 7 / span 1;
        justify-content: center;
    }

    .teacher-name, .teacher-email, .teacher-phone,.teacher-text-area {
        width: 300px;
        height: 40px;
    }
    
    .teacher-text-area{
        margin-right: 20px;
        height: 150px;
        width: 300px;
    }
    

    .teacher-login-register-div{
        height: 900px;
    }
    
    .teacher-intro{
        top: 1472px;
    }
    
    .teacher-use-div{
        position: absolute;
        top: 1972px;
        height: 1700px;
    }
}

@media screen and (max-width:340px){
    .teacher-login-register-div{
        height: 1000px;
    }
    
    .teacher-intro{
        top: 1572px;
    }
    
    .teacher-use-div{
        position: absolute;
        top: 2072px;
        height: 1700px;
    }

    .teacher-login-info{
        height: 60%;
    }
    
    .teacher-form{
        height: 40%;
        top: 60%;
    }

    .teacher-login-div{
        height: 40%;
    }
    
    .teacher-signup-info{
        height: 60%;
        top: 40%;
    }

}