*{
    margin: 0px;
    padding: 0px;

    --primary-color1:#3f82b1;
    --primary-color2:#484848;
    --primary-color3:#c6e7f1;
    --secondary-color1:#9a9a9a;
    --secondary-color2:#83a3b3;
    --secondary-color3:#ffffff;
    --tertiary-color1:#5c6569;
    --tertiary-color2:#506771;
    --tetriary-color3:#668491;
}

.students-nav-div{
    position: absolute;
    top: 72px;
    width: 100%;
    height: 400px;
    display: grid;
    grid-template-rows: repeat(6, 1fr);
}

.students-nav{
    background-color: var(--tertiary-color1);
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom-width:1px;
    border-bottom-color: var(--tetriary-color3);
    border-bottom-style: solid;
    box-sizing: border-box;
}

.students-nav:hover .students-nav-text{
    color: var(--secondary-color3);
    text-decoration-color: var(--secondary-color3);
}

.students-nav:hover{
    cursor: pointer;
}

.students-nav-text{
    font-size: 1.25em;
    font-weight: 100;
    text-decoration: underline;
    color: var(--primary-color3);
    text-decoration-color: var(--primary-color3);
    text-decoration-thickness: 1.5px;
    text-decoration-skip: 20px;
}

.students-intro{
    position: absolute;
    top: 472px;
    width: 100%;
    height: 500px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.use-nettuni{
    position: absolute;
    top: 972px;
    height: 2000px;
    width: 100%;
    display: grid;
    grid-template-rows: repeat(13, 1fr);
}

.intro-div{
    position: absolute;
    height: 96%;
    width: 99%;
    border-radius: 20px;
    background-color: var(--tertiary-color1);
}

.intro-svg{
    position: absolute;
    top: 3%;
    height: 94%;
    width: 40%;
    display: grid;
    grid-template-rows: repeat(3,1fr);   
}

.student-vid-div{
    position: absolute;
    left: 40%;
    height: 100%;
    width: 60%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.minvid{
    position: absolute;
    height: 90%;
    width: 90%;
    border-radius: 20px;
    background-color: var(--primary-color1);
}


.student1, .student2, .student3{
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.student1{
    grid-row: 1 / span 1;
}

.student2{
    grid-row: 2 / span 1;
}

.student3{
    grid-row: 3 / span 1;
}

.student-svg-div{
    position: absolute;
    height: 100%;
    width: 100%;
}

.student-svg{
    position: absolute;
    height: 100%;
    width: 20%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.student-svg-text-div{
    position: absolute;
    height: 100%;
    width: 80%;
    left: 20%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.student-svg-text{
    font-size: 1.3em;
    font-weight: 400;
    font-family: Georgia, 'Times New Roman', Times, serif;
    color: var(--primary-color3);
    text-align: left;
    text-shadow: var(--primary-color3) 0px 0px 20px;
}

.svg-ene{
    position: absolute;
    height: 150px;
    width: 90px;
}


.use-1, .use-2, .use-3, .use-4, .use-5{
    position: absolute;
    border-radius: 10px;
    height: 94%;
    width: 99%;
    background-color: var(--tertiary-color1);
    left: 0.5%;
}

.use-1{
    grid-row: 1/span 2;
}

.use-2{
    grid-row: 3/span 2;
}

.use-3{
    grid-row: 5/span 3;
}

.use-4{
    grid-row: 8/span 3;
}

.use-5{
    grid-row: 11/span 3;
}

.use-header{
    height: 20%;
    width: 100%;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    background-color: var(--secondary-color2);
    display: flex;
    align-items: center;
    justify-content: center;
}

.header-text{
    color: var(--secondary-color3);
    font-size: 1.45em;
    font-weight: 450;
}

.use-para{
    width: 100%;
    top: 20%;
    height:80%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.under-text{
    text-decoration: underline;
    color: var(--primary-color3);
    text-decoration-color: var(--primary-color3);
    text-decoration-thickness: 1.5px;
    text-decoration-skip: 20px;
}

.under-text:hover{
    color: var(--secondary-color3);
    text-decoration-color: var(--secondary-color3);
    cursor: pointer;
}

.paragraph{
    font-family: Georgia, 'Times New Roman', Times, serif ;
    font-size: 1.3em;
    font-weight: 500;
    color: #d7d7d7;
    width: 100%;
}

.paraen{
    position: absolute;
    height: 80%;
    width: 90%;
    left: 10%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.use-para-svg{
    position: absolute;
    height: 80%;
    width: 10%;
    left: 0%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.svg-to{
    position: absolute;
    height: 100%;
    width: 100%;
}

@media screen and (max-width:950px) {
    .paragraph{
        font-size: 1.15em;
        font-weight: 460;
    }

    .intro-svg{
        width: 30%;  
    }
    
    .student-vid-div{
        left: 30%;
        width: 70%;
        justify-content: center;
    }

    .student-svg-text{
        font-size: 1em;
        font-weight: 400;
        text-align: left;
        margin-left: 5px;
    }
    
    .student-vid{
        position: absolute;
        height: 90%;
        width: 100%;
        border-radius: 20px;
        background-color: var(--primary-color1);
    }

}

@media screen and (max-width:700px) {

    .paragraph{
        font-size: 0.95em;
        font-weight: 400;
    }
    
    
}

@media screen and (max-width:600px) {

    .paragraph{
        font-size: 0.85em;
        font-weight: 360;
    }

    .intro-svg{
        display: none;
    }
    
    .student-vid-div{
        left: 0%;
        width: 100%;
        justify-content: center;
    }
    
    .minvid{
        position: absolute;
        height: 98%;
        width: 98%;
    }
    
    
}

@media screen and (max-width:400px) {

    .paragraph{
        font-size: 0.7em;
        font-weight: 330;
    }
    
    .paraen{
        width: 85%;
        left: 15%;
    }
    
    .use-para-svg{
        width: 15%;
    }
    
}